import Cookies from 'js-cookie'

export default {
  name: "Promotion",
  data: function () {
    return {
      queryRewardTraceByLoginIdRequest: 'promotion-service/1.0.0/rotary_table_activity/queryRewardTraceByLoginId',
      rotatingFlag: false,
      accessToken: '',
      emptyPrizeFlag: true,
      prizeData: [],
      loading: false
    }
  },
  computed: {
    userActivityId() {
      return this.$route.query.state;
    },
    identityCode() {
      return this.$route.query.state;

    },
    wechatAuthCode() {
      return this.$route.query.code
    },
    stateCode() {
      return this.$route.query.state
    },
    token() {
      return this.$route.params.token;
    }
    // canvasWidth() {
    //   return this.remUnit * 13.5 + 'px';
    // },
    // canvasHeight() {
    //   return this.remUnit * 13.5 + 'px';
    // },
  },
  watch: {
    token() {
      this.accessToken = this.token;

    }
  },
  beforeMount() {

  },
  mounted() {
    this.$remResizing({
      fontSize: 20,
    });
    console.log(this.$route.query.token)
    this.getMyPrizeData();
  },
  methods: {
    getMyPrizeData() {
      let that = this;
      this.loading = true;
      this.$http.get(this.$baseUrl + this.queryRewardTraceByLoginIdRequest, {
        params: {
          loginId: this.$route.query.loginId,
          productDetail: 'all'
        },
        headers: {
          'Authorization': 'Bearer ' + Cookies.get('wheel-accessToken')
        }
      }).then(response => {
        console.log(response)
        this.loading = false;
        response = response.data;
        this.emptyPrizeFlag = response.length === 0;
        this.prizeData = response;
      }).catch(error => {
        this.loading = false;
        console.log(error)
        if (error.status === 401) {
          this.$vux.confirm.show({
            showCancelButton: false,
            title: '当前手机号失效，请重新登录',
            onConfirm() {
              that.$router.push({
                name: 'wheel'
              });
              Cookies.remove('wheel-accessToken');
              Cookies.remove('wheel-loginId');
            }
          })
        }

      })
    }
  }
}





